@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800);

.snip {
  background-color: #000000;
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.55);
          box-shadow: 0 0 5px rgba(0, 0, 0, 0.55);
  color: #ffffff;
  float: left;
  font-family: 'Open Sans', Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6em;
  margin: 10px 1%;
  max-width: 550px;
  overflow: hidden;
  position: relative;
  text-align: left;
  width: 100%;
}
img{
 width :950px;
 height: 260px;
}

.text{
  background-color: rgba(130,160,180,0.4);
  line-height: 20px;
  padding-left: 5px;
}

.snip * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}

.snip img {
  max-width: 100%;
  vertical-align: top;
  position: relative;
  opacity: 0.45;
}

.snip figcaption {
  padding: 25px 20px 25px;
  position: absolute;
  bottom: 0;
  color: rgba(255,255,255,1);
  z-index: 1;
}

.snip .date {
  background-color: #fff;
  color: #333;
  font-size: 18px;
  font-weight: 800;
  min-height: 48px;
  min-width: 48px;
  padding: 10px 0;
  position: absolute;
  right: 15px;
  text-align: center;
  text-transform: uppercase;
  top: 0;
}

.snip .date span {
  display: block;
  line-height: 14px;
}

.snip .date .month {
  font-size: 11px;
}

.snip h3,
.snip p {
  margin: 0;
  padding: 0;
}

.snip h3 {
  font-weight: 800;
  letter-spacing: -0.4px;
}

.snip .hover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0,0,0,0.5);
  display: -ms-flexbox;
  display: flex;
  font-size: 65px;
  -ms-flex-pack: center;
      justify-content: center;
  opacity: 0;
}

.snip a {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
}

.snip:hover .hover,
.snip.hover .hover {
  -webkit-transition-delay: 0.1s;
  -o-transition-delay: 0.1s;
     transition-delay: 0.1s;
  opacity: 1;
}

.snip:hover figcaption,
.snip.hover figcaption {
  opacity: 0;
}

.snip:hover .date,
.snip.hover .date {
  -webkit-transform: translateY(-100%);
  -ms-transform: translateY(-100%);
      transform: translateY(-100%);
}

body {
  background-color: rgba(55,55,55,.6);
}


